
body {
 margin-left: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

/* button {
  width: 7rem;
} */

form {
  display: grid;
  justify-content: flex-start;
  display: none;
}

ul {
  /* list-style-type: circle; */
  padding-inline-start: 5px;
}

li button {
  font-size: 12px;
  line-height: 8px;
  text-align: center;
  height: 16px;
  width: 22px;
  color: red
}

h1 {
  color: lightblue;
  font-style: italic;
}

textarea {
  width: 60%;
}

input {
  margin: 0.5rem 1rem;
}
 .inMessage {
  display: none;
  margin: 0 auto;
  text-align: center;
  /* width: 60%; */
}
.note {
  display: grid;
  margin: 0 auto;
  grid-template-columns: 10fr 1fr;
  color: gray;
  padding-top: 0.2rem;
  font-size: 0.8rem;
  width: 85%;
}
.message {
  /* position:fixed; */
  color: cadetblue;
  background: lightgrey;
  font-size: 1.2rem;
  border-style: solid;
  border-radius: 3px;
  padding: 5px 5px;
  width: 30rem;
  text-align: center;
  /* margin-bottom: 10px; */
}
/* .important {
  color: lightblue
} */
.empty {
  position: fixed;
  top: 0px;
  right: 0;
  left: 30%;
  height: 2rem;
}
.exit {
  margin-top: 50px;
}
.userLog {
  /* position: fixed; */
  /* top: 0px; */
  margin-right: 20%;
  text-align: right;
  /* left: 80%; */
  height: 2rem;
  /* color: rgba(0, 139, 139, 0.678); */
  color: rgb(82, 187, 228);
  font-style: italic;
 
}
.signin {
  display: block;
}